// components/Status.tsx
import styles from "../styles/Status.module.css";
import { formatDate } from "../lib/date";

interface StatusType {
  id: number;
  date: string;
  content: string;
}

interface Props {
  statuses: StatusType[];
}

export default function Status({ statuses }: Props) {
  return (
    <>
      {statuses.map((status) => (
        <div key={status.id} className={styles.statusCard}>
          <p>{status.content}</p>
          <small>{formatDate(status.date, 2)}</small>
        </div>
      ))}
    </>
  );
}
