/**
 * Ê†ºÂºèÂåñÊó•Êúü
 * @param dateStr - Êó•ÊúüÂ≠óÁ¨¶‰∏≤
 * @param type - Ê†ºÂºèÁ±ªÂûãÔºö1 = Jan 24, 26 (ÈªòËÆ§), 2 = Jan 24, 26 8h05
 */
export function formatDate(dateStr: string, type: 1 | 2 = 1): string {
  const date = new Date(dateStr);
  const year = date.getFullYear() % 100; // Âπ¥‰ªΩÂêé‰∏§‰Ωç
  const month = date.toLocaleString("en-US", { month: "short" }); // Jan, Feb...
  const day = date.getDate().toString().padStart(2, "0");

  if (type === 2) {
    const hours = date.getHours(); // ‰∏çË°•Èõ∂
    const minutes = date.getMinutes().toString().padStart(2, "0"); // ÂàÜÈíüË°•Èõ∂
    return `${month} ${day}, ${year} ${hours}h${minutes}`;
  }

  // type === 1
  return `${month} ${day}, ${year}`; // ÂéüÊ†ºÂºè
}
